import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import hotelSetting from './views/settings/hotelSetting'
import accountSetting from './views/settings/accountSetting'
import personalSetting from './views/settings/personalSetting'
import begin from './views/adSetting/begin'
import top from './views/adSetting/top'
import bottom from './views/adSetting/bottom'
// import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        leaf: true,//
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/main', component: Main, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        leaf: true,//
        hidden: true,
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/personalSetting', component: personalSetting, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/hotelSetting', component: hotelSetting, name: '' },
            { path: '/accountSetting', component: accountSetting, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/beginAd', component: begin, name: '' },
            { path: '/topAd', component: top, name: '' },
            { path: '/bottomAd', component: bottom, name: '' }
        ]
    }
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//
    //     children: [
    //         { path: '/page6', component: Page6, name: '' }
    //     ]
    // },
    // {
    //     path: '/',
    //     component: Home,
    //     name: 'Charts',
    //     iconCls: 'fa fa-bar-chart',
    //     children: [
    //         { path: '/echarts', component: echarts, name: 'echarts' }
    //     ]
    // },
    
];

export default routes;


