<template>
	<section>
		<!---->
		<p></p>

		<el-form :model="ruleForm" :rules="ruleFormRules" ref="ruleForm" label-width="100px" class="demo-ruleForm" style="margin-top:20px;">
			<el-form-item label="">
				<el-select v-model="userindex" placeholder="" size="small" @change="changeUser">
					<el-option v-for="(item,index) in users" :key="item.value" :label="item.username" :value="index">
					</el-option>
				</el-select>
			</el-form-item>

			<el-form-item label="" prop="username">
				<el-input v-model="ruleForm.username" class="w_350" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="password">
				<el-input v-model="ruleForm.password"  class="w_350" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="" prop="name">
				<el-input v-model="ruleForm.name"  class="w_350" placeholder=""></el-input>
			</el-form-item>

			<el-form-item label="">
				<!-- http://upload-z1.qiniu.com -->
				<el-upload
					class="avatar-uploader"
					action="//up.qbox.me/"
					:show-file-list="false"
					:data="uploadform"
					:on-success="handleAvatarSuccess"
					:before-upload="beforeAvatarUpload">
					<img v-if="ruleForm.avatar" :src="ruleForm.avatar" class="avatar">
					<i v-else class="el-icon-plus avatar-uploader-icon"></i>
				</el-upload>
				<el-input v-model="ruleForm.avatar"  style="width:500px;" placeholder=""></el-input>
				<p></p>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')" style="width:150px;"></el-button>
				<el-button @click="inituserinfo"></el-button>
			</el-form-item>
		</el-form>

	</section>
</template>
<script>
//import NProgress from 'nprogress'
export default {
  data() {
    return {
			userindex: 0,
			users:[],
			ruleForm: {
				username: "",
				password: "",
        name: "",
        avatar: ""
      },
      ruleFormRules: {
				selectuser: [{ required: true, message: ""}],
				username: [{ required: true, message: "", trigger: "blur" }],
				password: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }]
			},
			uploadform: {
        token: ""
      }
		};
  },
  methods: {
		changeUser(){
			let _this = this;
			let data = _this.users[_this.userindex]
			_this.ruleForm.username = data.username;
			_this.ruleForm.password = data.password;
			_this.ruleForm.name = data.name;
			_this.ruleForm.avatar = data.avatar;
		},
		handleAvatarSuccess(response, file, fileList) {
      this.ruleForm.avatar = `${this.host.imgBaseUrl}${response.key}`;
    },
    beforeAvatarUpload(file) {
      let isLt2M = file.size / 1024 / 1024 < 2;
      let _this = this;

      if (!isLt2M) {
        _this.$message.error(" 2MB!");
      } else {
        let params = {
          fileName: file.name
        };

        return axios
          .get(_this.host.baseUrl + "/uploadimg", { params })
          .then(res => {
            if (res.data.code === "1") {
              _this.uploadform = {
                key: res.data.result.fileName,
                token: res.data.result.uploadToken
              };
            } else {
              _this.$message.error("");
            }
          })
          .catch(() => {
            _this.$message.error("");
          });
      }

      return isLt2M;
    },
    resetSucc(res) {
      let _this = this;
      this.$message({
        message: "",
        type: "success"
      });
    },
    resetError(error) {
      this.$message.error("");
    },
    submitForm(formName) {
      let _this = this;
      // console.log(formName)
      _this.$refs[formName].validate(valid => {
        if (valid) {
          let url = _this.host.baseUrl + "/accountsetting";
          let user = JSON.parse(sessionStorage.getItem("user"));
          let data = _this.ruleForm;
          data.userid = _this.users[_this.userindex].userid;
          // alert('submit!');
          _this.ajax.http("put", url, data, _this.resetSucc, _this.resetError);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
		},
		inituserinfo (){
			this.ruleForm = {
				username: "admin",
				password: "123456",
        name: "",
        avatar: "https://raw.githubusercontent.com/taylorchen709/markdown-images/master/vueadmin/user.png"
      }
		}
	},
  mounted() {
		let _this = this;
		let url = _this.host.baseUrl + "/accountsetting";
		let user = JSON.parse(sessionStorage.getItem("user"));


		function getSucc(res){
			let data = res.data;
			_this.users = data;
			_this.changeUser();

		}
		function getError(error){
			console.log(error)
		}

		_this.ajax.http("get", url, {role:user.role}, getSucc, getError);
	}
};
</script>

<style>
.el-form-item.is-success .el-input__inner, .el-form-item.is-success .el-input__inner:focus, .el-form-item.is-success .el-textarea__inner, .el-form-item.is-success .el-textarea__inner:focus {
    border-color: #d8dce5;
}
</style>


